import { Component, OnInit, ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'mark1ang';
  messageReceived = '';
  currentMindMap = [];
  blockUrl = '';

  counter = 0;

  constructor(private changeDetector: ChangeDetectorRef) {
  }

  ngOnInit() {

    this.pushMapsIntoFirebase();

    this.messageReceived = 'watining for the message';

    // asking for the current mindMap

    chrome.runtime.onMessage.addListener(
      (req, sender, response) => {
        if (req && req.title === 'currentMap') {
          if (req.value && req.value.length > 0) {
            this.currentMindMap = req.value;
            this.changeDetector.detectChanges();
          } else {
            this.messageReceived = 'incoming map is empty';
          }
        }
      }
    );
    this.increaseCounter();
  }

  sendMessage(message: object) {
    chrome.runtime.sendMessage(message);
  }

  clearFromStorage() {
    this.sendMessage({
      title: 'clearData',
      value: 0
    });
  }

  getAllData() {
    this.sendMessage({
      title: 'getData',
      value: 0
    });
  }

  storeCurrentSessionMaps() {
    this.sendMessage({
      title: 'storeCurrentMap',
      value: 0
    });
  }

  increaseCounter() {
    ++this.counter;
  }

  pushMapsIntoFirebase() {
    chrome.storage.local.get(['maps'], (result) => {
      this.pushDataIntoFireStore(result);
    });
  }

  pushDataIntoFireStore(val) {

  }

  sendMessageForBloclUrls(text: string) {
    if (text.length > 0) {
      this.sendMessage({
        title: 'blockUrl',
        value: text
      });
    }
  }

}
